<template>
    <div>
        <div>row:</div>
        <input type="number" v-model="m" @change="update">
        <div>col:</div>
        <input type="number" v-model="n" @change="update">
    </div>
</template>

<script>
import { useStore } from 'vuex';
import { ref } from 'vue';
export default {
    setup() {
        const store=useStore();
        const n=ref(store.state.row);
        const m=ref(store.state.col);
        const update = ()=>{
            store.commit("update",[n,m]);
        }
        return {
            n,
            store,
            m,
            update
        }
    },
};
</script>

